import { createStore } from "vuex";
import axios from "axios";

const initTheme = localStorage.getItem("theme") || "dark";

export default createStore({
  state: {
    UI: {
      isHeader: true,
      isFooter: true,
    },
    theme: initTheme,
    isTest: true,
  },
  getters: {},
  mutations: {
    /** Change the theme of the app
     * @param {string} payload - A Theme string ["dark", "light"]
     * @returns {boolean} - A boolean value of the theme
     */
    changeTheme(state, payload): boolean {
      const theme = payload.theme;
      const isUpdate = state.theme !== theme;
      state.theme = theme;

      console.log(isUpdate);
      return isUpdate;
    },
  },
  actions: {
    async getTest() {
      const path = "http://localhost:5000/hello";
      const res = await axios.get(path);

      return res.data;
    },
  },
  modules: {},
});
